.line-through {
  text-decoration: line-through;
  user-select: none; }

.comment {
  display: grid;
  grid-template-columns: 38.197% 61.803%;
  grid-gap: 0;
  margin: 30px 0; }
  .comment .commentName {
    font-size: 1.6em;
    font-family: Roboto Mono, monospace;
    margin-bottom: 0.78125em;
    color: #696969; }
  .comment .commentText {
    font-size: 1.4em;
    font-family: Roboto Mono, monospace;
    margin-bottom: 1.78571em;
    font-weight: 300; }

@media screen and (min-width: 767px) {
  .comment {
    grid-template-columns: 20% 80%; } }

.commentLeftColumn {
  text-align: center; }
  .commentLeftColumn svg {
    margin: 20px 0 0;
    width: 40%;
    height: auto; }

@media screen and (min-width: 767px) {
  .commentLeftColumn img {
    max-width: 60px; } }

.commentRightColumn {
  background: transparent; }
